.input-group {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  text-align: left;
  position: relative;
}
.input-group .input-icon {
  width: 100%;
  padding: 0 40px 0 20px;
  text-align: left;
  color: #333;
}
.input-group .input-icon::-webkit-input-placeholder {
  font-size: 12px;
  color: #B2B2B2;
}
.input-group .input-icon::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.input-group .input-icon::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
.input-group .head {
  font-size: 12px;
  line-height: 34px;
  width: 4rem;
  height: 34px;
  background: #F6F6F6;
  border: 1px solid #E5E5E5;
  border-radius: 17px;
}
.input-group .onfous {
  font-size: 12px;
  line-height: 34px;
  width: 8.3rem;
  height: 34px;
  background: #F6F6F6;
  border: 1px solid #E5E5E5;
  border-radius: 17px;
}
.input-group .small {
  font-size: 12px;
  line-height: 34px;
  width: 3rem;
  height: 30px;
  background: #fff;
  border: 1px solid #B2B2B2;
  border-radius: 17px;
}
.input-group .domain {
  font-size: 14px;
  line-height: 34px;
  width: 3.3rem;
  height: 30px;
  background: #fff;
  border: 1px solid #E5E5E5;
  border-radius: 3px;
}
.input-group .domain::-webkit-input-placeholder {
  font-size: 14px;
  color: #B2B2B2;
}
.input-group .search-icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: -10px;
  cursor: pointer;
}
.input-group .domain-input + .search-icon {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -7px;
}



