.header-wrap {
  width: 100%;
  height: 58px;
  background: #fff;
  color: #333;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}
.header-wrap.header-shadow {
  -webkit-box-shadow: 0 2px 6px 0 rgba(197, 197, 197, 0.5);
          box-shadow: 0 2px 6px 0 rgba(197, 197, 197, 0.5);
}
.header-wrap .header-box {
  width: 14.4rem;
  min-width: 1024px;
  margin: 0 auto;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.header-wrap .header-box .header-menu {
  -webkit-flex: 1 1;
      -ms-flex: 1 1;
          flex: 1 1;
  min-width: 940px;
}
.header-wrap .header-box .header-menu .logo-icon {
  width: 124px;
  margin-top: 14px;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.header-wrap .header-box .header-menu ul {
  display: inline-block;
}
.header-wrap .header-box .header-menu ul li {
  display: inline-block;
  font-size: 15px;
  text-align: center;
  margin-right: 0.6rem;
  line-height: 58px;
  position: relative;
  cursor: pointer;
}
.header-wrap .header-box .header-menu ul li.active {
  position: relative;
  font-weight: 600;
}
.header-wrap .header-box .header-menu ul li.active:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: #FF7C5C;
  border-radius: 3px;
}
.header-wrap .header-box .header-menu .input-group {
  margin-top: 13px;
}
.header-wrap .header-box .header-right {
  min-width: 202px;
  margin-right: 0.5rem;
}
.header-wrap .header-box .header-right ul li {
  display: inline-block;
  font-size: 15px;
  text-align: center;
  margin-right: 0.3rem;
  vertical-align: middle;
  line-height: 58px;
}
.header-wrap .header-box .header-right ul li .point-login {
  cursor: pointer;
}
.header-wrap .header-box .header-right ul li .logined-text {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 0.5px solid #b2b2b2;
  border-radius: 13px;
  width: 108px;
  padding: 3px 10px 4px 5px;
  font-size: 12px;
  vertical-align: middle;
  position: relative;
  top: -1px;
}
.header-wrap .header-box .header-right ul li .logined-text .yuan-box {
  display: inline-block;
  width: 14px;
  height: 14px;
  background: #ddd;
  border-radius: 50%;
  vertical-align: middle;
  margin-top: -3px;
  margin-right: 5px;
}
.header-wrap .header-box .header-right ul li:last-child {
  margin-right: 0;
}
.header-wrap .header-box .header-right ul li .language-toggle {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 72px;
  height: 24px;
  line-height: 18px;
  position: relative;
}
.header-wrap .header-box .header-right ul li .language-toggle img {
  width: 100%;
  height: 100%;
}
.header-wrap .select-wrap {
  position: absolute;
  top: 27px;
  left: -3px;
  width: 120px;
  background: #FFFFFF;
  border: 1px solid #b2b2b2;
  border-radius: 3px;
  z-index: 1;
}
.header-wrap .select-wrap ul li {
  display: block;
  width: 100%;
  text-align: left;
  margin-right: 0 !important;
  line-height: 40px !important;
  border-bottom: 1px solid #b2b2b2;
}
.header-wrap .select-wrap ul li:last-child {
  border-bottom: none;
}
.header-wrap .select-wrap ul li a {
  color: #000;
  font-size: 12px;
  display: block;
  width: 100%;
  height: 100%;
}
.header-wrap .select-wrap ul li:hover {
  background: #ddd;
}



